  }
  .third {
    background-image: url('https://firebasestorage.googleapis.com/v0/b/truck-llc.appspot.com/o/ernesto.jpg?alt=media&token=9bcac2e7-3cb9-4550-9ddb-f145a09fe290');
  }
</style>